import Util from "../../../Tools/Util";

export default class BounsItem {
    private node: cc.Node;
    private data: any;
    private button: cc.Button;
    private Free_amount: cc.Label;
    public Init(node: cc.Node, data: any, onclickCallback: (bankType: number) => void) {
        this.node = node;
        this.node.active = true;
        this.data = data;
        this.button = this.node.getComponent(cc.Button);
        this.Free_amount = this.node.getChildByName("Free_amount").getComponent(cc.Label);
        this.Free_amount.string = "100";
        Util.SetClickAction(this.button, () => {
            onclickCallback(Number(this.Free_amount.string));
        });
    }
    public RemoveSelf() {
        this.node.removeFromParent();
    }
}